.homeSlide h1 {
  font-size: 45px;
}
.homeSlide p {
  margin: 20px 0;
}
.homeSlide ul {
  position: absolute;
  bottom: 20px;
}
.homeSlide ul li {
  border: 2px solid #b8b7b7;
  border-radius: 50%;
  padding: 10px;
}
.homeSlide ul li button {
  background: transparent !important;
  color: transparent !important;
}
.homeSlide ul li button::before {
  font-size: 15px;
  color: #0f3460;
  position: absolute;
  top: 2px;
  left: 0;
  margin: auto;
}
.right img {
  width: 300px;
  height: 250px;
}
.homeSlide .box {
  display: flex;
}
.homeSlide .box .left {
  width: 70%;
}
.homeSlide .box .right {
  width: 30%;
}
.category {
  width: 300px;
  z-index: 999;
  background-color: #fff;
}
