/* -------------------------Head--------------------------- */
.head {
  background-color: #0f3460;
  padding: 10px 0;
  color: #fff;
}
.head i {
  margin-right: 10px;
}
.head label {
  margin-right: 30px;
  font-size: 13px;
}

@media (max-width: 800px) {
  .head {
    display: none;
  }
}

/* -------------------------Head--------------------------- */
/* -------------------------Search--------------------------- */
.search {
  padding: 20px 0;
}
.search .logo .h1 {
  width: auto;
  height: auto;
  color: #e94560;
  font-weight: 800;
  font-size: 1.3rem;
  font-style: italic;
}
.search .width {
  width: 20%;
}
.search-box {
  width: 80%;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 50px;
}
.search-box i {
  width: 5%;
  text-align: center;
  font-size: 20px;
  opacity: 0.5;
  padding: 15px 20px;
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon-circle {
  width: 50px;
  height: 50px;
  cursor: pointer;
}
.search-box input {
  width: 75%;
  padding: 10px;
}
::placeholder {
  font-size: 15px;
}
.search-box span {
  width: 20%;
  opacity: 0.5;
  border-left: 2px solid rgba(0, 0, 0, 0.1);
  padding: 10px;
}
.cart {
  position: relative;
}
.search .cart span {
  position: absolute;
  top: -10px;
  right: 0;
  width: 20px;
  border-radius: 50%;
  text-align: center;
  background-color: #e94560;
  font-size: 12px;
  color: #fff;
}
.search .f_flex {
  justify-content: end;
}

@media (max-width: 1030px) {
  .search-box i {
    width: 5%;
  }
  .search-box input {
    width: 65%;
  }
  .search-box span {
    width: 25%;
  }
}
@media (max-width: 830px) {
  .search-box {
    width: 60%;
  }
  .search-box i {
    width: 5%;
  }
  .search-box input {
    width: 55%;
  }
  .search-box span {
    width: 30%;
  }
}
@media (max-width: 750px) {
  .search-box {
    width: 40%;
  }
  .search-box i {
    width: 5%;
  }
  .search-box input {
    width: 95%;
  }
  .search-box span {
    width: 30%;
    display: none;
  }
}
/* -------------------------Search--------------------------- */
.categories {
  background-color: #f6f9f6;
  padding: 0 30px;
  border-radius: 5px;
  height: 6vh;
  margin-top: 5px;
}
.categories span {
  margin-right: 20px;
  font-size: 30px;
  margin-top: 10px;
}
.categories h4 {
  font-weight: 500;
  margin-top: 10px;
}
.categories h4 i {
  margin-left: 10px;
}
.header {
  position: sticky;
  top: 70px;
  z-index: 1100;
  background-color: #fff;
}
.search {
  position: sticky;
  top: -15px;
  z-index: 1100;
  background-color: #fff;
}
header {
  height: 8vh;
  box-shadow: -1px 15px 17px -8px rgba(0, 0, 0, 0.1);
}
header ul {
  line-height: 8vh;
}
header ul li {
  margin-left: 30px;
}
header ul li a {
  transition: 0.5s;
  font-size: 15px;
  font-weight: 400;
}
header ul li a:hover {
  color: #e94560;
}
.open {
  display: none;
}
.search .active {
  height: 12vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  background-color: #fff;
  box-shadow: -1px 11px 24px -11px rgba(0, 0, 0, 0.2);
  transition: 0, 5s;
}

@media (max-width: 1000px) {
  header ul {
    padding-top: 30px;
  }
  .nav-links-MobileMenu {
    position: absolute;
    display: block;
    list-style-type: none;
    left: 0;
    top: 0;
    transition: 0.5s;
    width: 60%;
    height: 100vh;
    background-color: #fff;
    z-index: 888;
    box-shadow: rgba(50, 50, 93, 0.23) 0 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0 30px 60px -30px;
  }
  header ul li {
    width: 100%;
    transition: all 0.5s ease-in-out;
    text-transform: uppercase;
    line-height: 70px;
  }
  header .link {
    display: none;
  }
  .close,
  .open {
    position: absolute;
    top: 35px;
    right: 20px;
    color: #ff014f;
    display: block;
    font-size: 25px;
    z-index: 999;
  }
  .open {
    top: 20%;
    right: 30px;
    background-color: #e94560;
    color: #fff;
  }
  .close {
    right: 45%;
  }
  .close .home-btn {
    background: linear-gradient(145deg, #e2e8ec, #ffff);
    color: #ff014f;
    border-radius: 50%;
    padding: 0;
    width: 60px;
    height: 60px;
    line-height: 60px;
  }
}
@media (max-width: 800px) {
  .open {
    top: 15%;
    right: 30px;
    background-color: #e94560;
    color: #fff;
  }
}
